"use client";

import { Input } from "@nextui-org/input";
import { Button } from "@nextui-org/button";
import { useState } from "react";
import Conversa from "@/components/conversa";

import {ScrollShadow} from "@nextui-org/scroll-shadow";

var msgDiego = ['oie', 'tudo bem', 'já fez o trabalho do chat?', 'acho bom!'];
var msgUser = ['oii, tudo bem?', 'já sim, sor'];

export default function Home() {
  const [clicou, setClicou] = useState(false);
  return (
    <div className="md:flex h-screen">
      <section className="border-2 border-purple-700 rounded-lg md:w-96 md:pb-80 mr-12 mb-12 h-3/4">
        <p className="p-4 font-bold text-xl">Salas</p>
        <div className="p-4 flex">
          <img
            src="diegoCandido.jpg"
            className="w-1/4 rounded-lg mr-6"
            alt=""
          />
          <div onClick={()=> setClicou(true)}>
            <p className="font-bold">Diego Candido</p>
            <p>{msgDiego[2]}</p>
          </div>
        </div>
      </section>
      <main className="border-2 border-violet-700 rounded-lg md:w-3/4 h-3/4 flex flex-col">
        <div className="p-4 flex justify-between h-fit-content">
          <div className="flex">
            <img
              src="diegoCandido.jpg"
              className="h-12 rounded-lg mr-6 "
              alt=""
            />
            <div>
              <p className="font-bold">Diego Candido</p>
              <p>Online</p>
            </div>
          </div>
          <div className="justify-items-end" onClick={()=> setClicou(false)}>
            <img src="x.svg" className="w-8" alt="" />
          </div>
        </div>
        <Conversa></Conversa>
        <div className="flex mt-6 h-1/5 justify-center">
          <div className="flex w-full mt-8 flex-wrap md:flex-nowrap gap-4 ml-4">
            <Input type="Text" label="Envie uma mensagem..." />
          </div>
          <Button radius="full" className="mt-8 w-16 h-14 ml-4  mr-4 bg-violet-500" variant="shadow" isIconOnly color="secondary">
            <img src="paper-plane-right.svg" className="w-1/2" alt="" />
          </Button>
        </div>
      </main>
    </div>
  );
}
